import React from 'react';
import { Card, CardContent, CardActions } from '../Material/Card';
import { TextInputWithLabel } from '../Material/Form/Inputs/Text';
import { Button } from '../Material/Buttons';
import { SelectWithLabel } from '../Material/Form/Inputs/Select';

import Styles from '../Material/Styles.module.css';


class NodeExpansion extends React.Component {

    interactorTypes = [
        { type: 'disabled', label: 'Disabled' },
        { type: 'on_start', label: 'On Start' },
        { type: 'on_end', label: 'On End'},
        // { type: 'custom_time', label: 'After x seconds' },
    ];

    constructor(props) {
        super(props);

        this.state = {
            node: {
                id: '',
                title: '',
                url: '',
                parent_id: false,
                interactor: {enabled: false, type: 'custom_time'},
            },
        };

        this.handleChange = this.handleChange.bind(this);
        this.updateNode = this.updateNode.bind(this);
        this.toggleInteractor = this.toggleInteractor.bind(this);
        this.changeParent = this.changeParent.bind(this);

    }

    handleChange(e) {
        e.preventDefault();
        const value = e.target.value;
        // console.log(e.target.name);
        let node = {...this.state.node};
        if (e.target.name === 'interactor_type') {
            node.interactor.enabled = value !== 'disabled'; 
            node.interactor.type = value;
        } else {
            node[e.target.name] = value;
        }

        this.setState({node});
    }

    toggleInteractor() {
        let node = {...this.state.node};
        node.interactor.enabled = !node.interactor.enabled;
        this.setState({node});
    }

    changeParent(id) {
        let node = {...this.state.node};
        node.parent_id = id;
        this.setState({node});
    }

    updateNode() {
        this.props.updateNode(this.state.node);
    }

    componentDidMount() {
        this.setState({node: this.props.node});
    }

    render() {
        const { node } = this.state;
        // console.log('node: ', node);
        return (
            <Card
                className={Styles.elevation1}
                position="absolute"
                top="50%"
                left="50%"
                width="300px"
                margin="-50px 0 0 -125px"
                zIndex="10"
            >
                <CardContent>
                    <TextInputWithLabel 
                        label="Title"
                        name="title" 
                        width="75%"
                        value={node.title}
                        onChange={this.handleChange} 
                    />
                    <TextInputWithLabel
                        label="Video Url"
                        name="url"
                        value={node.url}
                        spellcheck="false"
                        onChange={this.handleChange}
                    />
                    <SelectWithLabel 
                        label="Show Interaction"
                        selected={this.state.node.interactor.enabled ? this.state.node.interactor.type : 'disabled'}
                        items={this.interactorTypes}
                        handleChange={this.handleChange}
                        marginBottom="0"

                    />
                </CardContent>
                <CardActions>
                    {/* TODO when cancel, revert state changes to prevstate */}
                    <Button
                        variant="text"
                        size="small"
                        onClick={ (e) => this.props.closeNodeExpansion(e, 0) }
                    >
                        cancel
                    </Button>
                    <Button 
                        variant="text" 
                        color="primary" 
                        size="small"
                        onClick={this.updateNode}
                    >save</Button>
                </CardActions>
            </Card>
        );
        return (
            <span className="node-expansion">
                <input 
                    onChange={this.handleChange} 
                    name="title" 
                    value={node.title}
                />
                <input 
                    onChange={this.handleChange} 
                    name="url" 
                    value={node.url}
                />
                <h5>Interactor</h5>
                <label>Enabled </label>
                <input type="checkbox" value="" checked={ node.interactor.enabled ? 'checked' : ''} onChange={this.toggleInteractor}/>

                {
                    node.parent_id > 0 
                        ?   <>
                            <label>Move to:</label>
                            {
                                
                                // todo exclude current parent_id and child node ids
                                this.props.availableParents.map(p => (
                                    <span 
                                        key={p.id} 
                                        onClick={() => this.changeParent(p.id)}
                                        style={{padding: '5px'}}>
                                        {p.title}
                                    </span>
                                ))
                            }
                            </>
                        : ''
                }
                
                <button onClick={this.updateNode}>done</button>
            </span>
        );
    }

}

export default NodeExpansion;